@import 'header.scss';

$body-font: Helvetica;

$bg-dark: #455A64;
$white: #fff;

body, html {
    padding: 0;
    margin: 0;

    vertical-align: middle;

    font-family: $body-font;

    background-color: $bg-dark;
    color: $white;

    text-align: center;
}

.layout_main {
	height: 100vh;
	display: grid;
	grid-template-areas: "header" "header_separator" "content";
	grid-template-columns: 100%;
	grid-template-rows: fit-content(100%) fit-content(100%) auto;
}

main.content {
	background-color: $bg-dark;
}